import react from 'react'
import { Animator } from '@ricky-fn/react-scroll-motion'
import classNames from 'classnames'

const BrowserTab = (props) => {
  return (
    <Animator
      animation={props.animation}
      className={classNames('tab', props.className)}
      style={props.style}
    >
      <svg width="100%" height="100%" viewBox="0 0 100 7" className="d-block">
        <text x="6" y="6" fontSize="7" fill="#fff">
          {props.children}
        </text>
      </svg>
    </Animator>
  )
}

export default BrowserTab
