export default function computeMotionRange(motionIn, motionOut, fn) {
  let realProgress = 0
  return (progress) => {
    if (progress <= motionIn) {
      realProgress = 0
    } else if (progress >= motionOut) {
      realProgress = 1
    } else {
      realProgress = (progress - motionIn) / (motionOut - motionIn)
    }

    return fn(realProgress)
  }
}
