import { computeMotionRange, computeStyle } from '../utils/'

export const TranslateYIn = (
  motionIn,
  motionOut,
  initial,
  target,
  unit = 'px'
) => ({
  in: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `translateY(${computeStyle(initial, target, p, unit)})`
      ),
    },
  },
})

export const TranslateYOut = (
  motionIn,
  motionOut,
  initial,
  target,
  unit = 'px'
) => ({
  out: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `translateY(${computeStyle(initial, target, p, unit)})`
      ),
    },
  },
})
