import React, { useState, useEffect } from 'react'
import { AppDataContext } from '../stores/AppDataContext'
import { PDFObject } from 'react-pdfobject'
import { ScrollContainer, ScrollPage } from '@ricky-fn/react-scroll-motion'

import initialAppData from '../constants/initialAppData'
import { detectDeviceType } from '../utils'

import './page.css'

import HeroSection from '../sections/hero'
import WorkSection from '../sections/work'
import ProjectSection from '../sections/projects'
import ContactSection from '../sections/contact'
import ResumeSection from '../sections/Resume'

const Page = () => {
  const [appData, setAppData] = useState({
    ...initialAppData,
    supportsPDFs: PDFObject.supportsPDFs(),
  })
  const { deviceType, isContentReady, showResumePDF } = appData

  React.useEffect(() => {
    setAppData({
      ...appData,
      deviceType: detectDeviceType(),
    })

    window.onresize = () => {
      setAppData((prevState) => ({
        ...prevState,
        deviceType: detectDeviceType(),
      }))
    }

    document.onreadystatechange = () => {
      if (document.readyState === 'complete') {
        clearInterval(window.loading)
        const elem = document.getElementById('abc')
        elem.className += ' complete'

        setTimeout(() => {
          document.getElementById('loader').className += ' d-none'

          setAppData((prevState) => ({
            ...prevState,
            isContentReady: true,
          }))
        }, 500)
      }
    }
  }, [])

  React.useEffect(() => {
    if (!isContentReady) return

    setTimeout(() => {
      setAppData((prevState) => ({
        ...prevState,
        isReadyForScroll: true,
      }))
    }, 1000)
  }, [isContentReady])

  // React.useEffect(() => {
  //   if (isScreenSizeSmall) {
  //     document.body.className = 'no-scroll'
  //   } else if (isReadyForScroll) {
  //     document.body.className = ''
  //   }
  // }, [isScreenSizeSmall, isReadyForScroll])

  const setShowResumePDF = (showResumePDF) => {
    if (!appData.supportsPDFs && showResumePDF) {
      return window.open('/playground_assets/resume.pdf')
    }

    setAppData((prevState) => ({
      ...prevState,
      showResumePDF,
    }))
  }

  useEffect(() => {
    const images = document.querySelectorAll('img[data-src]')

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          const img = entry.target
          const src = img.getAttribute('data-src')

          img.setAttribute('src', src)
          img.removeAttribute('data-src')
          img.classList.remove('lazyload')
          observer.unobserve(img)
        }
      })
    })

    images.forEach((img) => {
      observer.observe(img)
    })
  }, [])

  return (
    <AppDataContext.Provider value={appData}>
      <div className="page-container">
        {appData.supportsPDFs && (
          <ResumeSection show={showResumePDF} setShow={setShowResumePDF} />
        )}
        <ScrollContainer>
          <ScrollPage
            pageHeight={
              deviceType === 'Desktop'
                ? 1800
                : deviceType === 'Tablet'
                ? 3200
                : 2200
            }
          >
            <HeroSection setShowResume={setShowResumePDF} />
          </ScrollPage>
          <ScrollPage
            pageHeight={
              deviceType === 'Desktop'
                ? window.innerHeight * 4
                : deviceType === 'Tablet'
                ? 800
                : deviceType === 'Mobile'
                ? 1500
                : window.innerHeight
            }
          >
            <WorkSection />
          </ScrollPage>
          {deviceType !== 'Desktop' && (
            <ScrollPage
              pageHeight={deviceType === 'Mobile' ? 1950 : window.innerHeight}
            >
              <ProjectSection />
            </ScrollPage>
          )}
          <ScrollPage
            pageHeight={deviceType === 'Mobile' ? 1393 : window.innerHeight * 2}
          >
            <ContactSection setShowResume={setShowResumePDF} />
          </ScrollPage>
        </ScrollContainer>
      </div>
    </AppDataContext.Provider>
  )
}

export default Page
