import { computeMotionRange, computeStyle } from '../utils/'

export const ScaleIn = (motionIn, motionOut, initial, target) => ({
  in: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `scale(${computeStyle(initial, target, p, '')})`
      ),
    },
  },
})

export const ScaleOut = (motionIn, motionOut, initial, target) => ({
  out: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `scale(${computeStyle(initial, target, p, '')})`
      ),
    },
  },
})
