import React, { useEffect, useContext } from 'react'
import classNames from 'classnames'
import { PDFObject } from 'react-pdfobject'
import { AppDataContext } from '../stores/AppDataContext'

const ResumeSection = ({ show, setShow }) => {
  const { isReadyForScroll, supportsPDFs } = useContext(AppDataContext)

  useEffect(() => {
    if (!isReadyForScroll) return
    document.body.className = show ? 'no-scroll' : ''
  }, [show, isReadyForScroll])

  return (
    <div
      className={classNames(
        'section-resume position-fixed top-0 start-0 vw-100 vh-100 z-2',
        {
          'd-none': !show,
        }
      )}
    >
      <PDFObject
        width="100vw"
        height="100vh"
        url="/playground_assets/resume.pdf"
        forcePDFJS={true}
      />
      <div className="icon-return" onClick={() => setShow(false)}>
        <div className="motion-layer d-flex flex-row align-items-center">
          <svg
            t="1679968612668"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="40"
            height="40"
            className="flex-1"
          >
            <path
              d="M541.052632 238.315789V0L29.052632 512l512 512V785.684211L267.368421 512l273.684211-273.684211z"
              fill="#2c2a2a"
            ></path>
            <path
              d="M424.421053 511.915789L543.578947 392.842105l119.073685 119.073684L543.578947 630.989474zM826.526316 427.789474h168.421052v168.421052h-168.421052z"
              fill="#2c2a2a"
            ></path>
          </svg>
          <span className="flex-1 text-secondary-emphasis text-center">
            Return
          </span>
        </div>
      </div>
    </div>
  )
}

export default ResumeSection
