import { computeMotionRange, computeStyle } from '../utils/'

export const TranslateXIn = (motionIn, motionOut, initial, target) => ({
  in: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `translateX(${computeStyle(initial, target, p, 'px')})`
      ),
    },
  },
})

export const TranslateXOut = (motionIn, motionOut, initial, target) => ({
  out: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `translateX(${computeStyle(initial, target, p, 'px')})`
      ),
    },
  },
})
