import { computeMotionRange, computeStyle } from '../utils/'

export const FadeIn = (motionIn, motionOut, initial, target) => ({
  in: {
    style: {
      opacity: computeMotionRange(motionIn, motionOut, (p) =>
        computeStyle(initial, target, p, '')
      ),
    },
  },
})

export const FadeOut = (motionIn, motionOut, initial, target) => ({
  out: {
    style: {
      opacity: computeMotionRange(motionIn, motionOut, (p) =>
        computeStyle(initial, target, p, '')
      ),
    },
  },
})
