import react, { useEffect, useRef, useContext } from 'react'
import classNames from 'classnames'
import './modal.css'

import { AppDataContext } from '../stores/AppDataContext'

const Modal = (props) => {
  const { isReadyForScroll, deviceType } = useContext(AppDataContext)

  const el = useRef(null)

  const handleClickEvent = ({ target }) => {
    if (el.current === target || target.dataset.role === 'close') props.onHide()
  }

  useEffect(() => {
    // prevent user from scrolling the whole page but only the modal content
    if (!isReadyForScroll) return
    document.body.className = props.show ? 'no-scroll' : ''
  }, [props.show, isReadyForScroll])

  return (
    <div
      ref={el}
      className={classNames('modal zn-1', {
        'modal-md': deviceType === 'Desktop' && props.size === 'medium',
        'modal-lg': deviceType === 'Desktop' && props.size === 'large',
        'z-3': props.show,
      })}
      style={{
        opacity: props.show ? 1 : 0,
        pointerEvents: props.show ? 'all' : 'none',
      }}
      onClick={handleClickEvent}
    >
      <div className="modal-dialog">{props.children}</div>
    </div>
  )
}

Modal.Header = (props) => (
  <div className="modal-header">
    {props.children}
    {props.closeButton && (
      <button className="close">
        <img src="/playground_assets/close.svg" data-role="close" />
      </button>
    )}
  </div>
)

Modal.Title = (props) => <h4 className="modal-title">{props.children}</h4>

Modal.Body = (props) => (
  <div className="modal-content">
    <div className="modal-container p-5 p-sm-3">{props.children}</div>
  </div>
)

export default Modal
