import { computeMotionRange, computeStyle } from '../utils/'

export const RotateIn = (motionIn, motionOut, initial, target) => ({
  in: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `rotate(${computeStyle(initial, target, p, 'deg')})`
      ),
    },
  },
})

export const RotateOut = (motionIn, motionOut, initial, target) => ({
  out: {
    style: {
      transform: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `rotate(${computeStyle(initial, target, p, 'deg')})`
      ),
    },
  },
})
