import react, { useState } from 'react'
import { Animator } from '@ricky-fn/react-scroll-motion'
import { computeMotionRange } from '../utils'

const browserTabFadeIn = (motionIn, motionOut, stay = true) => ({
  out: {
    style: {
      opacity: computeMotionRange(motionIn, motionOut, (p) =>
        (p > 0 && p < 1) || (p > 0 && stay) ? 1 : 0
      ),
    },
  },
})

const BrowserContent = ({ motionIn, motionOut, onClick, children }) => {
  return (
    <Animator
      animation={browserTabFadeIn(motionIn, motionOut, false)}
      className="browser-page"
    >
      <div className="overlay z-1">
        <button className="btn btn-lg btn-primary" onClick={onClick}>
          READ MORE
        </button>
      </div>
      {children}
    </Animator>
  )
}

export default BrowserContent
