import React, { useContext } from 'react'
import { Animator } from '@ricky-fn/react-scroll-motion'

import { AppDataContext } from '../stores/AppDataContext'
import { computeMotionRange, computeStyle } from '../utils'

import { TranslateYOut } from '../animations'
import classNames from 'classnames'

const ContactSection = ({ setShowResume }) => {
  const { deviceType } = useContext(AppDataContext)

  const backgroundMoveIn = (motionIn, motionOut) => ({
    in: {
      style: {
        left: computeMotionRange(motionIn, motionOut, (p) =>
          computeStyle(-100, 0, p, '%')
        ),
      },
    },
  })

  const Sticky = (motionIn, motionOut) => ({
    out: {
      style: {
        position: (p) =>
          p >= motionIn && p <= motionOut ? 'fixed' : 'relative',
      },
    },
  })

  return (
    <Animator
      animation={deviceType !== 'Mobile' ? Sticky(0, 1) : {}}
      className="section-contact d-flex top-0 start-0 w-100 vh-md-100 vh-lg-100"
    >
      <Animator
        animation={backgroundMoveIn(0.8, 0.9)}
        className="page-background"
      />
      <div className="container-fluid d-flex d-sm-block position-relative z-2">
        <Animator
          animation={deviceType === 'Mobile' ? Sticky(0, 1) : {}}
          className="page-photo flex-1 position-sm-absolute top-sm-0 w-sm-100 vh-sm-100 opacity-sm-25"
        ></Animator>
        <div className="page-content flex-1 h-md-100 h-lg-100">
          <Animator
            animation={
              deviceType !== 'Mobile' ? TranslateYOut(0, 1, 0, -1170) : {}
            }
            className="page-container w-100 d-flex flex-column pl-4 pt-5 position-relative z-1"
          >
            <div className="typography">
              <p
                className={classNames('font-weight-light text-primary mb-4', {
                  h1: deviceType === 'Desktop',
                  h2: deviceType !== 'Desktop',
                })}
              >
                To stay connected:
              </p>
              <a
                className="h4 font-weight-light text-primary"
                href="mailto:ricky.jc2022@gmail.com"
              >
                <span className="icon text-center d-inline-block">
                  <img
                    src="/playground_assets/envelope.svg"
                    className="d-inline align-middle"
                  />
                </span>
                ricky.jc2022@gmail.com
              </a>
              <a
                className="h4 font-weight-light text-primary my-3"
                href="https://www.linkedin.com/in/ricky-jiang/"
                target="_blank"
              >
                <span className="icon text-center d-inline-block">
                  <img
                    src="/playground_assets/linkedIn.svg"
                    className="d-inline align-bottom"
                  />
                </span>
                LinkedIn Profile
              </a>
              <p
                className="h4 font-weight-light text-primary cursor-pointer"
                onClick={setShowResume}
              >
                <span className="icon text-center d-inline-block">
                  <img
                    src="/playground_assets/document.svg"
                    className="d-inline align-bottom"
                  />
                </span>
                View resume
              </p>
            </div>
            <div className="typography">
              <p className="h1 font-weight-light text-primary mb-4">
                This website:
              </p>
              <div className="d-flex align-items-center mb-2">
                <span className="h4 font-weight-light text-primary mb-2 w-lg-25 w-md-50 w-sm-50">
                  is powered by:
                </span>
                <img
                  src="/playground_assets/reactjsar2119534-ubh.svg"
                  alt="React.js"
                  className="page-reactjsar211"
                />
              </div>
              <div className="d-flex align-items-center">
                <span className="h4 font-weight-light text-primary w-lg-25 w-md-50 w-sm-50">
                  is hosted on:
                </span>
                <img
                  src="/playground_assets/githublogo9530-j4sd.svg"
                  alt="Github"
                  className="page-githublogo"
                />
              </div>
            </div>
            <p className="h1 font-weight-light text-primary typography">Bye!</p>
          </Animator>
        </div>
      </div>
    </Animator>
  )
}

export default ContactSection
