import React, { useState } from 'react'
import { Animator, batch } from '@ricky-fn/react-scroll-motion'
import { computeStyle, computeMotionRange, isSafari } from '../utils'
import {
  TranslateXOut,
  TranslateYOut,
  BlurOut,
  ScaleOut,
  RotateOut,
  FadeOut,
} from '../animations'
import BrowserTab from './browserTab'
import BrowserContent from './BrowserContent'
import DomaniModal from './modals/projects/Domani'
import TessaractModal from './modals/projects/Tessaract'
import TurbineModal from './modals/projects/Turbine'
import Covid19VisualizerModal from './modals/projects/Covid19Visualizer'

const ProjectSection = () => {
  const keyframe = {
    'animation-started': 0,
    'zoom-completed': 0.15,
    'show-1st-project': 0.15,
    'hide-1st-project': 0.3,
    'show-2st-project': 0.3,
    'hide-2st-project': 0.45,
    'show-3st-project': 0.45,
    'hide-3st-project': 0.6,
    'show-4st-project': 0.6,
    'move-out-logos-started': 0.75,
    'move-out-logos-completed': 0.8,
    'zoom-laptop-started': 0.75,
    'zoom-laptop-completed': 0.8,
    'move-laptop-started': 0.78,
    'move-laptop-completed': 0.82,
    'animation-completed': 1,
  }

  const Sticky = (motionIn, motionOut) => ({
    out: {
      style: {
        position: (p) =>
          p >= motionIn && p <= motionOut ? 'fixed' : 'absolute',
      },
    },
  })

  const MacBookMotion = () => ({
    out: {
      style: {
        position: 'fixed',
        marginLeft: computeMotionRange(
          keyframe['animation-started'],
          keyframe['zoom-completed'],
          (p) => computeStyle(0, -600, p, 'px')
        ),
        marginTop: computeMotionRange(
          keyframe['animation-started'],
          keyframe['zoom-completed'],
          (p) => computeStyle(30, -300, p, 'px')
        ),
        width: computeMotionRange(
          keyframe['animation-started'],
          keyframe['zoom-completed'],
          (p) => computeStyle(566, 1200, p, 'px')
        ),
      },
    },
  })

  const reactMotion = () => ({
    out: {
      style: {
        marginLeft: computeMotionRange(
          keyframe['animation-started'],
          keyframe['zoom-completed'],
          (p) => computeStyle(-50, -800, p, 'px')
        ),
        marginTop: computeMotionRange(
          keyframe['animation-started'],
          keyframe['zoom-completed'],
          (p) => computeStyle(-58, -570, p, 'px')
        ),
        transform: (p) => `rotate(${360 * (1 - p) * 6}deg)`,
      },
    },
  })

  const browserTabFadeIn = (motionIn, motionOut, stay = true) => ({
    out: {
      style: {
        opacity: computeMotionRange(motionIn, motionOut, (p) =>
          (p > 0 && p < 1) || (p > 0 && stay) ? 1 : 0
        ),
      },
    },
  })

  const activateTabIn = (motionIn, motionOut) => ({
    in: {
      className: {
        'with-shape': computeMotionRange(
          motionIn,
          motionOut,
          (p) => p > 0 && p < 1
        ),
      },
    },
  })

  const activateTabOut = (motionIn, motionOut) => ({
    out: {
      className: {
        'with-shape': computeMotionRange(
          motionIn,
          motionOut,
          (p) => p > 0 && p < 1
        ),
      },
    },
  })

  const [showDomaniModal, setShowDomaniModal] = useState(false)
  const handleDomaniModalClose = () => setShowDomaniModal(false)
  const handleDomaniModalShow = () => setShowDomaniModal(true)

  const [showTessaractModal, setShowTessaractModal] = useState(false)
  const handleTessaractModalClose = () => setShowTessaractModal(false)
  const handleTessaractModalShow = () => setShowTessaractModal(true)

  const [showVisualizerModal, setShowVisualizerModal] = useState(false)
  const handleVisualizerModalClose = () => setShowVisualizerModal(false)
  const handleVisualizerModalShow = () => setShowVisualizerModal(true)

  const [showTurbineModal, setShowTurbineModal] = useState(false)
  const handleTurbineModalClose = () => setShowTurbineModal(false)
  const handleTurbineModalShow = () => setShowTurbineModal(true)

  return (
    <Animator className="mac-root" animation={Sticky(0, 1)}>
      {!isSafari && (
        <Animator
          animation={TranslateYOut(
            keyframe['move-out-logos-started'],
            keyframe['move-out-logos-completed'],
            0,
            -1500
          )}
          className="page-logos"
        >
          <Animator
            animation={batch(
              FadeOut(0, 1, 1, 1),
              BlurOut(0.1, 0.15, 10, 0),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                1000,
                150
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                900,
                100
              ),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                7,
                1
              )
            )}
            className="vs"
          >
            <img alt="vs" src="/playground_assets/logos/vs.svg" />
          </Animator>
          <Animator
            animation={batch(
              BlurOut(0.1, 0.15, 3, 0),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                -2000,
                -850
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                -900,
                -250
              ),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                10,
                2
              ),
              FadeOut(0, 1, 1, 1)
            )}
            className="webpack"
          >
            <img alt="webpack" src="/playground_assets/logos/webpack.svg" />
          </Animator>
          <Animator
            animation={batch(
              BlurOut(0.1, 0.15, 4, 0),
              RotateOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                9,
                9
              ),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                800,
                45
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                -1000,
                -490
              ),
              FadeOut(0, 1, 1, 1),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                5,
                1
              )
            )}
            className="npm"
          >
            <img alt="npm" src="/playground_assets/logos/npm.svg" />
          </Animator>
          <Animator
            animation={batch(
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -930
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -70
              ),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                1,
                2
              ),
              BlurOut(0.1, 0.15, 0, 2)
            )}
            className="chrome"
          >
            <img alt="chrome" src="/playground_assets/logos/chrome.svg" />
          </Animator>
          <Animator
            animation={batch(
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -350
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                -800,
                -570
              ),
              BlurOut(0.1, 0.15, 3, 0)
            )}
            className="vue"
          >
            <img alt="vue" src="/playground_assets/logos/vue.svg" />
          </Animator>
          <Animator
            animation={batch(
              RotateOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                -36,
                -36
              ),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                440
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -60
              ),
              BlurOut(0.1, 0.15, 0, 3),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                2,
                1
              )
            )}
            className="figma"
          >
            <img alt="figma" src="/playground_assets/logos/figma.svg" />
          </Animator>
          <Animator
            animation={batch(
              RotateOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                24,
                24
              ),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                400
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                120
              ),
              BlurOut(0.1, 0.15, 0, 3),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0.5,
                1.5
              )
            )}
            className="nodejs"
          >
            <img alt="node.js" src="/playground_assets/logos/nodejs.svg" />
          </Animator>
          <Animator
            animation={batch(
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0.5,
                1.5
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                140
              ),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -360
              ),
              RotateOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                15
              ),
              BlurOut(0.1, 0.15, 0, 2)
            )}
            className="cypress"
          >
            <img alt="cypress" src="/playground_assets/logos/cypress.svg" />
          </Animator>
          <Animator animation={reactMotion()} className="react">
            <img alt="react" src="/playground_assets/logos/react.svg" />
          </Animator>
          <Animator
            animation={batch(
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                250
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -50
              ),
              BlurOut(0.1, 0.15, 0, 2),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                2,
                1
              )
            )}
            className="sass"
          >
            <img alt="sass" src="/playground_assets/logos/sass.svg" />
          </Animator>
          <Animator
            animation={batch(
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0.5,
                2
              ),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -520
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -270
              ),
              RotateOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                18
              ),
              BlurOut(0.1, 0.15, 0, 3)
            )}
            className="aws"
          >
            <img alt="aws" src="/playground_assets/logos/aws.svg" />
          </Animator>
          <Animator
            animation={batch(
              {
                out: {
                  style: {
                    rotate: '-25deg',
                  },
                },
              },
              BlurOut(0.1, 0.15, 0, 4),
              ScaleOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0.5,
                1.5
              ),
              TranslateXOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                0,
                -730
              ),
              TranslateYOut(
                keyframe['animation-started'],
                keyframe['zoom-completed'],
                -100,
                -140
              )
            )}
            className="typescript"
          >
            <img
              alt="typescript"
              src="/playground_assets/logos/typescript.svg"
            />
          </Animator>
        </Animator>
      )}
      <Animator
        animation={batch(
          ScaleOut(
            keyframe['animation-started'],
            keyframe['zoom-completed'],
            1,
            2.4
          ),
          TranslateXOut(
            keyframe['animation-started'],
            keyframe['zoom-completed'],
            0,
            -147
          ),
          TranslateYOut(
            keyframe['animation-started'],
            keyframe['zoom-completed'],
            0,
            -83
          ),
          {
            out: {
              style: {
                opacity: (p) => (p <= keyframe['zoom-completed'] ? 1 : 0),
              },
            },
          }
        )}
        className="position-relative z-0"
      >
        <img src="/playground_assets/Artboard.svg" className="macbook" />
        <div className="browser-container">
          <div className="browser-header">
            <div className="browser-tabs">
              <BrowserTab
                animation={batch(
                  activateTabIn(0, 1),
                  activateTabOut(
                    keyframe['animation-started'],
                    keyframe['zoom-completed']
                  )
                )}
                style={{ opacity: 1 }}
              >
                New Tab
              </BrowserTab>
            </div>
          </div>
        </div>
      </Animator>
      <Animator
        animation={batch(
          ScaleOut(
            keyframe['zoom-laptop-started'],
            keyframe['zoom-laptop-completed'],
            1,
            0.5
          ),
          TranslateYOut(
            keyframe['move-laptop-started'],
            keyframe['move-laptop-completed'],
            0,
            -2000
          ),
          BlurOut(
            keyframe['zoom-laptop-started'],
            keyframe['zoom-laptop-completed'],
            0,
            6
          ),
          {
            out: {
              style: {
                opacity: (p) => (p > keyframe['zoom-completed'] ? 1 : 0),
              },
            },
          }
        )}
        className="w-100 position-absolute opacity-0"
      >
        <img src="/playground_assets/Artboard.svg" className="macbook big" />
        <div className="browser-container">
          <div className="browser-header">
            <div className="browser-tabs">
              <BrowserTab style={{ opacity: 1 }}>New Tab</BrowserTab>
              <BrowserTab
                animation={batch(
                  browserTabFadeIn(
                    keyframe['animation-started'],
                    keyframe['zoom-completed']
                  ),
                  activateTabOut(
                    keyframe['show-1st-project'],
                    keyframe['hide-1st-project']
                  )
                )}
              >
                DOMANI | APP
              </BrowserTab>
              <BrowserTab
                animation={batch(
                  browserTabFadeIn(
                    keyframe['animation-started'],
                    keyframe['zoom-completed']
                  ),
                  activateTabOut(
                    keyframe['show-2st-project'],
                    keyframe['hide-2st-project']
                  )
                )}
              >
                Tessaract
              </BrowserTab>
              <BrowserTab
                animation={batch(
                  browserTabFadeIn(
                    keyframe['animation-started'],
                    keyframe['zoom-completed']
                  ),
                  activateTabOut(
                    keyframe['show-3st-project'],
                    keyframe['hide-3st-project']
                  )
                )}
              >
                Covid-19 Visualizer
              </BrowserTab>
              <BrowserTab
                animation={batch(
                  browserTabFadeIn(
                    keyframe['animation-started'],
                    keyframe['zoom-completed']
                  ),
                  activateTabOut(
                    keyframe['show-4st-project'],
                    keyframe['animation-completed']
                  )
                )}
              >
                Turbine JavaScript
              </BrowserTab>
            </div>
            <div className="browser-url">
              <Animator
                animation={browserTabFadeIn(
                  keyframe['show-1st-project'],
                  keyframe['hide-1st-project'],
                  false
                )}
              >
                dapp.domani.finance
              </Animator>
              <Animator
                animation={browserTabFadeIn(
                  keyframe['show-2st-project'],
                  keyframe['hide-2st-project'],
                  false
                )}
              >
                tessaract.io
              </Animator>
              <Animator
                animation={browserTabFadeIn(
                  keyframe['show-3st-project'],
                  keyframe['hide-3st-project'],
                  false
                )}
              >
                theworldcovid19.info
              </Animator>
              <Animator
                animation={browserTabFadeIn(
                  keyframe['show-4st-project'],
                  keyframe['animation-completed'],
                  false
                )}
              >
                github.com/ricky-fn/turbine
              </Animator>
            </div>
          </div>
          <div className="browser-body">
            <BrowserContent
              motionIn={keyframe['show-1st-project']}
              motionOut={keyframe['hide-1st-project']}
              onClick={handleDomaniModalShow}
            >
              <img
                src="/playground_assets/project-domani.png"
                className="w-100"
              />
            </BrowserContent>
            <BrowserContent
              motionIn={keyframe['show-2st-project']}
              motionOut={keyframe['hide-2st-project']}
              onClick={handleTessaractModalShow}
            >
              <img
                src="/playground_assets/project-tessaract.png"
                className="w-100"
              />
            </BrowserContent>
            <BrowserContent
              motionIn={keyframe['show-3st-project']}
              motionOut={keyframe['hide-3st-project']}
              onClick={handleVisualizerModalShow}
            >
              <img
                src="/playground_assets/project-covid19.png"
                className="w-100"
              />
            </BrowserContent>
            <Animator
              animation={browserTabFadeIn(
                keyframe['show-4st-project'],
                keyframe['animation-completed'],
                false
              )}
              className="browser-page"
            >
              <div className="overlay z-1">
                <button
                  className="btn btn-lg btn-primary"
                  onClick={handleTurbineModalShow}
                >
                  READ MORE
                </button>
              </div>
              <img
                src="/playground_assets/project-turbine.png"
                className="w-100"
              />
            </Animator>
          </div>
        </div>
      </Animator>
      <DomaniModal show={showDomaniModal} onHide={handleDomaniModalClose} />
      <TessaractModal
        show={showTessaractModal}
        onHide={handleTessaractModalClose}
      />
      <Covid19VisualizerModal
        show={showVisualizerModal}
        onHide={handleVisualizerModalClose}
      />
      <TurbineModal show={showTurbineModal} onHide={handleTurbineModalClose} />
    </Animator>
  )
}

export default ProjectSection
