import { computeMotionRange, computeStyle } from '../utils/'

export const BlurIn = (motionIn, motionOut, initial, target) => ({
  in: {
    style: {
      filter: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `blur(${computeStyle(initial, target, p, 'px')})`
      ),
    },
  },
})

export const BlurOut = (motionIn, motionOut, initial, target) => ({
  out: {
    style: {
      filter: computeMotionRange(
        motionIn,
        motionOut,
        (p) => `blur(${computeStyle(initial, target, p, 'px')})`
      ),
    },
  },
})
